      randomPhrase () {
        return (this.fraseAleatoria = Math.floor(
          Math.random() * this.frasesMotivacionais.length,
        ))
      },

      setTimeNow () {
        var date = new Date()
        var day = date.getDate()
        var month = date.getMonth() + 1
        var year = date.getFullYear()
        if (month < 10) {
          month = '0' + month
        }
        if (day < 10) {
          day = '0' + day
        }
        this.now = day + '/' + month + '/' + year
        const m = [
          'JANEIRO',
          'FEVEREIRO',
          'MARÇO',
          'ABRIL',
          'MAIO',
          'JUNHO',
          'JULHO',
          'AGOSTO',
          'SETEMBRO',
          'OUTUBRO',
          'NOVEMBRO',
          'DEZEMBRO',
        ]
        this.monthInWords = m[date.getMonth()]
        if (this.editedIndex === -1) {
          this.editedItem.DATA = this.now
          this.editedItem.MÊS = this.monthInWords
        }
      },
      setEditTime () {
        const timeElapsed = Date.now()
        const rightNow = new Date(timeElapsed)
        this.timeNow = rightNow.toLocaleString('pt-BR')
      },

      setComercial () {
        if (this.editedIndex === -1) {
          this.editedItem.COMERCIAL = this.user
        }
      },

      formatDate (tempDate) {
        if (!tempDate) {
          return null
        }

        const [year, month, day] = tempDate.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate (tempDate) {
        if (!tempDate) {
          return null
        }

        const [day, month, year] = tempDate.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },

      parseMonetary () {
        this.editedItem.CUSTOTAXA = this.editedItem.CUSTOTAXA.replace('R$ ', '')
        this.editedItem.VALORCOBRADO = this.editedItem.VALORCOBRADO.replace('R$ ', '')
        this.editedItem.VALORBOLETO = this.editedItem.VALORBOLETO.replace('R$ ', '')
        this.editedItem.SALDO = this.editedItem.SALDO.replace('R$ ', '')
      },

      getColor (STATUS) {
        if (STATUS === 'PAGO') {
          return 'blue lighten-4'
        } else if (STATUS === 'A VENCER') {
          return 'green lighten-4'
        } else if (STATUS === 'TAXA PAGA') {
          return 'purple lighten-4'
        } else if (STATUS === 'A COBRAR') {
          return 'red lighten-4'
        } else if (STATUS === 'CONFERIR') {
          return 'orange lighten-4'
        } else if (STATUS === 'VENCIDO') {
          return 'red lighten-4'
        } else if (STATUS === 'NÃO COBRAR' || STATUS === 'ACERTO') {
          return 'red lighten-4'
        } else if (STATUS === '') {
          return ''
        } else { return 'grey lighten-2' }
      },

      getIcon (STATUS) {
        if (STATUS === 'PAGO') {
          return 'mdi-check-all blue--text'
        } else if (STATUS === 'A VENCER' || STATUS === 'A DEPOSITAR') {
          return 'mdi-cash-clock'
        } else if (STATUS === 'TAXA PAGA') {
          return 'mdi-check'
        } else if (STATUS === 'A COBRAR') {
          return 'mdi-clock-alert-outline'
        } else if (STATUS === 'CONFERIR') {
          return 'mdi-email-alert-outline'
        } else if (STATUS === 'VENCIDO') {
          return 'mdi-alert red--text'
        } else if (STATUS === 'NÃO COBRAR' || STATUS === 'ACERTO') {
          return 'mdi-cash-remove red--text'
        } else { return 'mdi-list-status' }
      },

      fetchLicences () {
        return fetch('https://sheetdb.io/api/v1/afxq0nl1f0gvg')
          .then((r) => r.json())
          .then((r) => {
            this.licences = r
          })
      },

      greaterId () {
        return axios.get('https://sheetdb.io/api/v1/afxq0nl1f0gvg').then((r) => {
          console.log(r.data.length)
          this.id = r.data.length + 1
          for (let i = 0; i < r.data.length; i++) {
            Number(this.id) === Number(r.data[i].ID) ? this.repeated = true : this.repeated = false
          }
          // console.log('id: ' + this.id)
          // console.log('id duplicado? ' + this.repeated)
          if (this.editedIndex === -1) {
            this.repeated === true ? alert('ID duplicado. Favor contatar suporte e suspender novos lançamentos até que a correção seja confirmada.') : this.editedItem.ID = this.id
          }
        })
      },

      editItem (item) {
        this.err.includes('Duplicado')
        ? alert('Não atualizar lançamentos e contatar suporte.')
        : (this.editedIndex = this.licences.indexOf(item), this.editedItem = Object.assign({}, item), this.dialog = true)
      },
      deleteItem (item) {
        this.editedIndex = this.licences.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      editItemConfirm () {
        // salva edição
        if (this.editedIndex > -1) {
          axios.put(`https://sheetdb.io/api/v1/afxq0nl1f0gvg/ID/${this.editedItem.ID}`, {
            data: {
              SOLICITANTE: `${this.editedItem.SOLICITANTE}`,
              PLACA: `${this.editedItem.PLACA}`,
              NUMTAXA: `${this.editedItem.NUMTAXA}`,
              CUSTOTAXA: `${this.editedItem.CUSTOTAXA}`,
              DATAPAGAMENTOTAXA: `${this.editedItem.DATAPAGAMENTOTAXA}`,
              BANCO: `${this.editedItem.BANCO}`,
              AET: `${this.editedItem.AET}`,
              EMISS\u00c3O: `${this.editedItem.EMISSÃO}`,
              VENCIMENTO: `${this.editedItem.VENCIMENTO}`,
              VALORCOBRADO: `${this.editedItem.VALORCOBRADO}`,
              SALDO: `${this.editedItem.SALDO}`,
              STATUS: `${this.editedItem.STATUS}`,
              NUMBOLETO: `${this.editedItem.NUMBOLETO}`,
              VALORBOLETO: `${this.editedItem.VALORBOLETO}`,
              VENCIMENTOBOLETO: `${this.editedItem.VENCIMENTOBOLETO}`,
              PAGAMENTOBOLETO: `${this.editedItem.PAGAMENTOBOLETO}`,
              OBSERVA\u00c7\u00c3O: `${this.editedItem.OBSERVAÇÃO}`,
              EDICAO: `${this.editedItem.EDICAO}` + ` ${this.user} ${this.timeNow}.`,
            },
          })
            .then((response) => {
              if (response.status === 200 || response.status === 201) {
                this.showUpdate = !this.showUpdate
                this.fetchLicences()
                setTimeout(() => {
                  this.showUpdate = !this.showUpdate
                }, 1500)
              } else {
                this.showError = !this.showError
                setTimeout(() => {
                  this.showError = !this.showError
                  window.location.reload()
                }, 1500)
              }
            })
          this.dialogSave = false
          setTimeout(() => {
            this.close()
          }, 200)
        } else {
          this.save()
        }
      },
      deleteItemConfirm () {
        this.licences.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      closeDialogSave () {
        this.dialogSave = false
      },
      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      validateField () {
        this.$refs.form.validate()
      },

      clone () {
        if (this.editedIndex > -1) {
          this.tempEmpresa = this.editedItem.EMPRESA
          this.tempSolicitante = this.editedItem.SOLICITANTE
          this.tempPgBoleto = this.editedItem.PAGAMENTOBOLETO
          this.tempComercial = this.editedItem.COMERCIAL
          this.tempPlaca = this.editedItem.PLACA
          this.tempEstado = this.editedItem.ESTADO
          this.tempStatus = this.editedItem.STATUS
          this.tempDataPgTaxa = this.editedItem.DATAPAGAMENTOTAXA
          this.tempBanco = this.editedItem.BANCO
          this.tempCusto = this.editedItem.CUSTOTAXA
          this.tempTaxa = this.editedItem.NUMTAXA
          this.tempDataEmissao = this.editedItem.EMISSÃO
          this.tempDataVencimento = this.editedItem.VENCIMENTO
          this.tempAET = this.editedItem.AET
          this.tempCobrado = this.editedItem.VALORCOBRADO
          this.tempVencimentoBoleto = this.editedItem.VENCIMENTOBOLETO
          this.tempBoleto = this.editedItem.VALORBOLETO
          this.tempNumBoleto = this.editedItem.NUMBOLETO
          this.tempSaldo = this.editedItem.SALDO
          this.tempObs = this.editedItem.OBSERVAÇÃO
          // 3.fechar o popup OK
          setTimeout(() => {
            this.close()
          }, 300)
          // 4.abrir popup de novo pedido
          setTimeout(() => {
            document.getElementById('newItemBtn').click()
          }, 300)
          // 5.popular os campos com as informações salvas anteriormente
          setTimeout(() => {
            this.editedItem.EMPRESA = this.tempEmpresa
            this.editedItem.SOLICITANTE = this.tempSolicitante
            this.editedItem.PAGAMENTOBOLETO = this.tempPgBoleto
            this.editedItem.COMERCIAL = this.tempComercial
            this.editedItem.PLACA = this.tempPlaca
            this.editedItem.ESTADO = this.tempEstado
            this.editedItem.STATUS = this.tempStatus
            this.editedItem.DATAPAGAMENTOTAXA = this.tempDataPgTaxa
            this.editedItem.BANCO = this.tempBanco
            this.editedItem.CUSTOTAXA = this.tempCusto
            this.editedItem.NUMTAXA = this.tempTaxa
            this.editedItem.EMISSÃO = this.tempDataEmissao
            this.editedItem.VENCIMENTO = this.tempDataVencimento
            this.editedItem.AET = this.tempAET
            this.editedItem.VALORCOBRADO = this.tempCobrado
            this.editedItem.VENCIMENTOBOLETO = this.tempVencimentoBoleto
            this.editedItem.VALORBOLETO = this.tempBoleto
            this.editedItem.NUMBOLETO = this.tempNumBoleto
            this.editedItem.SALDO = this.tempSaldo
            this.editedItem.OBSERVAÇÃO = this.tempObs
          }, 300)
        } else {
          this.showCloneBtn = false
        }
      },
      saveStatus (item) {
        this.setEditTime()
        this.$nextTick(() => {
          this.editedIndex = this.licences.indexOf(item)
          axios.put(`https://sheetdb.io/api/v1/afxq0nl1f0gvg/ID/${this.editedIndex + 1}`, {
              data: {
                STATUS: `${item.STATUS}`,
                EDICAO: `${item.EDICAO}` + ` ${this.user}, ${this.timeNow}. `,
              }
            })
            .then((response) => {
              if (response.status === 200 || response.status === 201) {
                this.showUpdate = !this.showUpdate
                this.fetchLicences()
                setTimeout(() => {
                  this.showUpdate = !this.showUpdate
                }, 1500)
              } else if (response.status === 429) {
                alert('O limite de solicitações foi excedido! 10.000 licenças foram lançadas esse mês! Entre em contato com o suporte para liberar mais espaço.')
                window.location.reload()
              } else if (response.status === 401 || response.status === 403 || response.status === 404) {
                alert('Erro de servidor. Entre em contato com o suporte.')
                window.location.reload()
              } else {
                this.showError = !this.showError
                setTimeout(() => {
                  this.showError = !this.showError
                  window.location.reload()
                }, 1500)
              }
            })
          this.close()
        })
      },

      save () {
        var form = document.getElementById('sheetdb-form')
        if (this.editedIndex > -1) {
          this.dialogSave = true
        } else {
          // salva novo
          if (!this.id) {
            alert('lançamento sem ID. Favor contatar suporte antes de prosseguir.')
          }
          fetch(form.action, {
            method: 'POST',
            body: new FormData(form),
          }).then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showPost = !this.showPost
              this.fetchLicences()
              setTimeout(() => {
                this.showPost = !this.showPost
              }, 1500)
            } else if (response.status === 429) {
              alert('Erro ao Salvar' + 'O limite de solicitações foi excedido! 10.000 licenças foram lançadas esse mês! Entre em contato com o suporte para liberar mais espaço.')
            } else {
              alert('Erro ao Salvar' + 'Erro de servidor. Entre em contato com o suporte.')
            }
            this.close()
          })
        }
      },
    },
  }
</script>

<style>
.grey-label .v-label {
  color: grey;
}
.v-text-field input {
  height: 100% !important
}
.v-input input {
  max-height: 100% !important;
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease
}

.v-enter-from,
.v-leave-to {
  opacity: 0
}
</style>
